export default {
	data() {
		return {
			banner:'',
			creditlimit:''
		}
	},
	methods: {
		toFrontal(index) {
			uni.navigateTo({
				url:"../frontal/frontal?index="+index
			})
		}
	},
	onLoad() {
		this.request.httpRequest('api/course/index','GET').then(res => {
			this.banner = res.data.retval.banner
			this.creditlimit = res.data.retval.list_data
		})
	}
}
